.header {
    height: 35px;
    padding: 10px 15px;
    display: flex;
    gap: 20px;
    background-color: #095484;
    box-shadow: 0 2px 5px #ccc;
}

.logo img {
    height: 100%;
    width: auto;
}

nav {
    display: flex;
    align-items: center;
}

.nav-list {
    list-style-type: none;
    display: inline-flex;
    gap: 20px;
    margin: 0;
    padding: 0;
    color: white;
    font-size: 20px;
}

.nav-list li {
    font-weight: 400;
    line-height: 1.2;  
    cursor: pointer;
    color: inherit;
} 

.nav-list li a {
    color: inherit;
    text-decoration: none;
}

.nav-list li:hover {
    color: rgb(245, 197, 24);
    text-shadow: 3px 3px 3px rgba(49, 22, 14, 0.7);
}